particlesJS.load('root', JSON.stringify(particlesJSON));

function changeParticles() {
  var changeOptions = [['image', whale], ['image', cat], ['image', balloon], ['circle', '']];
  var currentChange = 0;

  var changeParticlesSlide = setInterval(function() {
    $('.particles-js-canvas-el').fadeOut('slow');
    particlesJSON.particles.shape.type = changeOptions[currentChange][0];
    particlesJSON.particles.shape.image.src = changeOptions[currentChange][1];
    particlesJSON.particles.size.value = 20;
    if ($('.particles-js-canvas-el').length) {
      particlesJS.load('root', JSON.stringify(particlesJSON));
    } else {
      clearInterval(changeParticlesSlide);
    }

    if ((currentChange + 1) === changeOptions.length) {
      currentChange = 0;
    } else {
      currentChange++;
    }
  }, 20000)
}

changeParticles();
ReactDOM.render(<App />, document.getElementById('root'));
