import React, { Component } from 'react';
import './App.css';
import $ from "jquery";
import orangeJuice from './assets/orange_juice.png';
import socks from './assets/socks.png';
import cat from './assets/cat.png';
import whale from './assets/whale.png';
import greys from './assets/greys_anatomy.jpg';
import pierogi from './assets/pierogi.jpg';
import GoT from './assets/GoT.jpg';
import mail from './assets/mail.png';
import questionMark from './assets/question_mark.png';
import pizza from './assets/pizza.png';
import me from './assets/me.png';

/*
  Task 1 - Make a working slide, this allows the slide to be "activated", and it should move to the next slide [COMPLETE]
  Task 2 - Add working back button [COMPLETE]
  Task 3 - Stop slides from going above max count [COMPLETE]
  Task 4 - Add custom CSS [COMPLETE]
  Task 5 - Allow each slide to have its own custom CSS
  Task 6 - Add custom functionality to the slides
  Main Task - Add slides [COMPLETE]


  Task x - Add an "ending" slide that allows you to go back
  Task x - Check CSS for notes
  Task x - Append "keyAlert" to element if timeout count has been reached
  Task x - Remove custom css styles each slide
  Task x - Add working next button (skip button)
  Task x - Hide "back button" on first page
  Task x - Allow only clickable slides when typewriter effect is finished

  Task Extra - Add auto-updating time for slide 2
*/

let curr_slide = 1;
var keyAlert = <h2 className="click_to">Click to continue!</h2>;
var typeWriterActive = false;
var globalSpentTime = '';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {slide: 1}; // Current state = current slide
  }

  handleSlide = () => {
    var _state = [this.state, this];
    // If not max slide count
    console.log(curr_slide, Object.keys(this.slides).length);
    setTimeout(function() {
      if (typeWriterActive !== true) {
        if (curr_slide !== Object.keys(_state[1].slides).length) {
          curr_slide++;
        }

        _state[1].setState({slide: curr_slide});
      }
    }, 50)
  }

  handlePagination = (e) => {
    var _state = this.state;
    function checkPageState(stopCount) {
        if (_state.slide === stopCount) {
          return true;
        }
    }

    if ($(e.target).attr('id').split('_')[0] === 'back') {
      // Check if page count has reached limit
      if (checkPageState(1)) {
        return false;
      }

      curr_slide--;
      this.setState({slide: curr_slide}); // Set state to previous slide count
    } else { // else "next"
      console.log('placeholder');
      // Make sure to handle state when skipping, i.e CSS
    }
  }

  fontEffects = {
    'rainbowFont': function(ele, slide_current) {
      var tempText = ele.text().split('');
      // Append each letter in a span
      ele.text('');
      tempText.map(x => ele.append(`<span class="font_effect">${x}</span>`));
      var childText = Object.values(ele.children('span.font_effect'));
      var textCount = 0;

      function rColor() {
        return Math.floor(Math.random() * 255) + 1;
      }

      var textInterval = setInterval(function() {
          if (slide_current !== curr_slide) {
            clearInterval(textInterval);
          }

          if (textCount !== 0) {
            $(childText[textCount - 1]).css({'font-size': '3.5rem', 'color': `rgb(${rColor()}, ${rColor()}, ${rColor()})`});
          }

          $(childText[textCount]).css({'font-size': '4rem', 'color': `rgb(${rColor()}, ${rColor()}, ${rColor()})`});
          if (textCount >= childText.length) {
            textCount = 0;
          } else {
            textCount++;
          }
      }, 75)


    }
  }

  componentDidMount() {
    if (curr_slide === 1) {
      // init
      this.fontEffects['rainbowFont']($('h1'), curr_slide);
    }
  }

  componentDidUpdate() {
    var _state = [this.state, this];
    var queue = [];
    var active = false;
    var countElements = $('.slide').find(':header, p');
    var elementCount = 0;

    $('.slide').find(':header, p').each(function() {
      /* Typewriter effect */
      var text_copy = $(this).text();
      var ele = $(this);
      $(this).text('');

      function setTypewriter() {
        var count = 0;
        typeWriterActive = true;
        var typeWriter = setInterval(function() {
          if (count >= text_copy.length) {
            clearInterval(typeWriter)
            queue.shift(); // Remove first from queue
            if (queue.length) {
              ele = queue[0][0];
              text_copy = queue[0][1];
              setTypewriter();
            }

            if (elementCount >= countElements.length - 1) {
              console.log('TYPEWRITER FINISHED');
              typeWriterActive = false;
            }
            elementCount++;
            return false;
          }

          ele[0].textContent += text_copy[count];
          count++;
        }, 60);
      }

      queue.push([$(this), text_copy]);
      if ($(this)[0] === queue[0][0][0]) {
        setTypewriter();
      }
    });

    function cssSlide() {
      var cssCurrent = _state[1].slidesCSS['slide_' + curr_slide];
      if (cssCurrent.length) { // If array has any 'levels'
        cssCurrent.map(function(c_style) {
          var css_values = Object.values(c_style)[0];
          $(Object.keys(c_style)[0]).css(css_values[0], css_values[1])
        });
      }
    }

    cssSlide();

    function slideFunc() {
      _state[1].slideFunctions['slide_' + curr_slide]();
    }

    slideFunc();
  }

  ItemsYouLike() {
    var likedItems = ['Orange Juice', 'Socks', 'Cats', 'Pierogi', 'Whales', 'Grey\'s Anatomy', 'Game of Thrones', 'Tyler'];
    const items = likedItems.map((c_item) =>
      <h3>You like {c_item}</h3>
    );

    return items;
  }

  slides = {
    'slide_1': <div>
    <h1>Happy Birthday Basia!</h1>
    {keyAlert}
    </div>,
    'slide_2': <div className="slide_con">
      <div className="container container_1">
        {keyAlert}
        <h1>Dear Barbara,</h1>
        <p>It's your birthday! You're 20 years old today (2 decades)</p>
        <p>It's been a blast knowing you, talking to you is the highlight of my day</p>
        <p>You mean a lot to me, so I'm glad I'm able to say happy birthday to someone so special.</p>
        <p>I feel like I've known you a lot longer than we've known each other... Here's how long it's really been</p>
      </div>
      <div className="container container_2">
        <div id="graph">
          <h2 id="week_count" className="date_count"></h2>
          <h2>Weeks</h2>

          <h2 id="day_count" className="date_count"></h2>
          <h2>Days</h2>

          <h2 id="hour_count" className="date_count"></h2>
          <h2>Hours</h2>

          <h2 id="minute_count" className="date_count"></h2>
          <h2>Minutes</h2>

          <h2 id="second_count" className="date_count"></h2>
          <h2>Seconds</h2>
        </div>
      </div>
    </div>,
    'slide_3': <div className="slide_con">
      {keyAlert}
      <div className="container">
        <h1>Basia,</h1>
        <h2 style={{'display': 'inline-block'}}>I've learnt so much about you over these past -</h2><h2 id="random_time"></h2>
        <h2>Here are a few of the highlights of things I know you like ;)</h2>
        {this.ItemsYouLike()}
        <div id="item_box">
          <div id="item_container">
          </div>
        </div>
      </div>
    </div>,
    'slide_4': <div className="slide_con">
      {keyAlert}
      <div className="container">
        <h1>Now this slide is a bit more personal</h1>
        <h2>I've been waiting for the perfect time to tell you something, but I didn't want it to be too soon.</h2>
        <h2>I feel like this is the perfect time to finally say it, so why not?</h2>
        <h2>I truly must say that I really do ...</h2>
        <div id="ytplayer">
        </div>
      </div>
    </div>,
    'slide_5': <div className="slide_con">
      {keyAlert}
      <div className="container">
        <h1>Continuing from the previous slide..</h1>
        <h2>You are a very special, unique and amazing person,</h2>
        <h2>It makes me happy I get to experience your 20th birthday with you</h2>
        <h2>Perhaps next time you'll invite me to the party ;)</h2>

        <h2>You are strong and independent.</h2>
        <h2>You are very smart and funny</h2>
        <h2>And I hope you always be you, nothing about you needs to change, you're amazing just the way you are.</h2>
        <h2>I'll be there when you need it, as I need my future Dogtor.</h2>

        <h3>Here's some stats from our daily talks.</h3>

        <div id="stats">
          <div className="stats_con">
            <h4>Total Messages</h4>
            <h5 id="total_messages" className="total_stats"></h5>
          </div>

          <div className="stats_con">
            <h4>Total Words</h4>
            <h5 id="total_words" className="total_stats"></h5>
          </div>
        </div>
      </div>
    </div>,
    'slide_6': <div className="slide_con">
      <div className="container">
        <h1>I should continue slides, but I ended up a bit short on time</h1>
        <h2>Someone kept me up a bit longer than I expected....</h2>
        <h2>Since I couldn't properly pick your gift, perhaps you'll help me out?</h2>
        <h3>Wait for it..</h3>
        <div id="gifts">
          <div className="gift_row_1">
            <div className="gift_type">
              <img src={pizza} alt="" className="image_gift"/>
              <p>Yes, Pizza. I'll get you a pizza! Preferebly something I could order ;)</p>
            </div>

            <div className="gift_type">
              <img src={socks} alt="" className="image_gift"/>
              <p>Is it.. SOCKS!?! Yes it is, I know you like to shop on AliExpress, so whatever type of socks you want - stationery, or anything else ;)</p>
            </div>
          </div>

          <div className="gift_row_2">
            <div className="gift_type">
              <img src={mail} alt="" className="image_gift"/>
              <p>Mail me a gift! - Note, this option will be a surprise. But there's a catch. 1. I need your address, and 2. this option may take the longest to get.. ;()</p>
            </div>

            <div className="gift_type">
              <img src={questionMark} alt="" className="image_gift"/>
              <p>Not enough options? I know.. Tyler should of tried harder. If this is the case, this option is for you! Allows you to pick what you want!</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  }

  slidesCSS = {
    'slide_1': [], // slide_1 css is init state
    'slide_2': [
      {'h1': ['font-size', '2.5rem']},
      {'p': ['font-size', '2.3rem']},
      {'.slide': ['align-items', 'baseline']}
    ],
    'slide_3': [
      {'h2': ['font-size', '2.5rem']}
    ],
    'slide_4': [
      {'h2': ['font-size', '1.8rem']}
    ],
    'slide_5': [],
    'slide_6': []
  }

  slideFunctions = {
    'slide_1': function() {
      $('h1').css('font-size', '50px');
    },

    'slide_2': function() {
      // Remove particles from DOM
      $('.particles-js-canvas-el').remove();

      $('#bg_container').fadeOut('slow');
      $('h1, h2, h3, p').css('color', '#777777');
      if (typeWriterActive) {
        var checkActive = setInterval(function() {
          if (typeWriterActive === false) {
            clearInterval(checkActive);
            getDates();
          }
        }, 1000)
      }

      function getDates() {
        var currentDate = (new Date).getTime();
        var dateMet = new Date('November 5, 18 12:11:00 GMT+00:00');
        var timeSpent = currentDate - dateMet;
        const to_num = (x) => {return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")};
        var datesTime = {
          'week': timeSpent / ((((1000 * 60) * 60) * 24) * 7),
          'day': timeSpent / (((1000 * 60) * 60) * 24),
          'hour': timeSpent / ((1000 * 60) * 60),
          'minute': timeSpent / (1000 * 60),
          'second': timeSpent / 1000
        }
        globalSpentTime = Object.values(datesTime).map(x => [Object.keys(datesTime)[Object.values(datesTime).indexOf(x)] + 's', to_num(Math.floor(x))]);
        console.log(globalSpentTime);

        $('.date_count').each(function() {
          $(this).animate({
              totalCount: datesTime[$(this).attr('id').split('_')[0]]
          }, {
              duration: 5000,
              step: function() {
                $(this).text(to_num(Math.floor(this.totalCount)));
              }, complete: function() {
                console.log('done');
              }
          });
        });
      }
    },
    'slide_3': function() {
      var randomDate = Math.floor(Math.random() * globalSpentTime.length)

      $('#bg_container').fadeIn('slow');
      $('body').css('background-color', 'black');

      $('#random_time').text(globalSpentTime[randomDate][1] + ' ' + globalSpentTime[randomDate][0]);
      // Guess the time it should take..
      setTimeout(function() {
        $('#random_time').fadeIn('slow');
      }, 3500);

      if (typeWriterActive) {
        var checkActive = setInterval(function() {
          if (typeWriterActive === false) {
            clearInterval(checkActive);
            addItems();
          }
        }, 1000)
      }


      function addItems() {
        var items = [orangeJuice, socks, cat, pierogi, whale, greys, GoT, me];
        var count = 0;

        var appendImages = setInterval(function() {
            $('#item_container').append(`<img src="${items[count]}" alt="" class="image_liked">`);
            if ((count + 1) >= items.length) {
              clearInterval(appendImages);
            } else {
              count += 1
            }
        }, 500);
      }
    },
    'slide_4': function() {
      var iframe = `<iframe id="existing-iframe-example"
              width="640" height="360"
              src="https://www.youtube.com/embed/n2hJA78YuWw?enablejsapi=1&autoplay=1"
              frameborder="0"
              style={{"border": "solid 4px #37474F"}}
      ></iframe>`
      if (typeWriterActive) {
        var checkActive = setInterval(function() {
          if (typeWriterActive === false) {
            clearInterval(checkActive);
            $('#ytplayer').append(iframe);
          }
        }, 1000)
      }

    },
    'slide_5': function() {
      var types = [['total_messages', 40251], ['total_words', 145327]]
      const to_num = (x) => {return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")};


      if (typeWriterActive) {
        var checkActive = setInterval(function() {
          if (typeWriterActive === false) {
            clearInterval(checkActive);
            addStats();
          }
        }, 1000)
      }

      function addStats() {
        for (var addStats = 0; addStats < types.length; addStats++) {
          $('#' + types[addStats][0]).each(function() {
            $(this).animate({
                totalCount: types[addStats][1]
            }, {
                duration: 5000,
                step: function() {
                  $(this).text(to_num(Math.floor(this.totalCount)));
                }, complete: function() {
                  console.log('done');
                }
            });
          });
        }
      }
    },
    'slide_6': function() {
      if (typeWriterActive) {
        var checkActive = setInterval(function() {
          if (typeWriterActive === false) {
            clearInterval(checkActive);
            $('#gifts').fadeIn('slow');
            $('#gifts').css('display', 'flex');
          }
        }, 1000)
      }
    }
  }

  render() {
    return (
      <div id="slide_container">
        <div id="bg_container"></div>
        <div id="slide_btns">
          <button type="button" id="back_btn" className="pagination_btn" onClick={this.handlePagination}>Back</button>
        </div>
        <div className="slide" onClick={this.handleSlide}>{this.slides[`slide_${this.state.slide}`]}</div>
      </div>
    );
  }
}

export default App;
